
















import Vue from 'vue';
import {Video} from '@/polyphony/youtube';

export default Vue.extend({
  name: 'VideoList',
  props: ['list'],
  components: {
    Video: () => import ('@/components/Video.vue'),
  },
  data() {
    return {
      videos: [],
    }
  },
  async created() {
    await Promise.all(this.list.map(
     (video: Video) => video.fetchVideoInfo()
    )).then(() => this.videos = this.list);
  },
})
